import Airtable from "airtable";
import React, { useState } from "react";

const MultiStepForm = () => {
  const base = new Airtable({
    apiKey: process.env.REACT_APP_AIRTABLE_ACCESS_TOKEN,
  }).base("app4ay5fqFKNY3V4V");

  const [step, setStep] = useState(1);
  const [formData, setFormData] = useState({});
  const [errors, setErrors] = useState({});
  const [submitted, setSubmitted] = useState(false);

  const steps = [
    { number: 1, name: "Basic Information" },
    { number: 2, name: "Person, Problem, Pain" },
    { number: 3, name: "Amplification, Aspiration" },
    { number: 4, name: "Story, Solution, System" },
    { number: 5, name: "Testimony, Transformation" },
    { number: 6, name: "Offer, Response" },
  ];

  const questions = [
    { id: "Full Name", label: "Your first and last name", step: 1 },
    { id: "Email", label: "Your email address", step: 1 },
    { id: "Company", label: "Your brand name", step: 1 },
    {
      id: "URL",
      label: "Enter the website address (URL) of your brand",
      step: 1,
    },
    // ???
    // {
    //   id: "analysis",
    //   label: "Have you already received a website analysis report?",
    //   step: 1,
    //   type: "radio",
    // },
    {
      id: "Q1: Describe what your brand does and for who?",
      label: "1. Describe what your brand does and for who?",
      step: 2,
    },
    {
      id: "Q2: What is the primary goal or desire that your product/service helps your customers achieve?",
      label:
        "2. What is the primary goal or desire that your product/service helps your customers achieve?",
      step: 2,
    },
    {
      id: "Q3: What is the main problem that prospects are facing that you help them solve?",
      label:
        "3. What is the main problem that prospects are facing that you help them solve?",
      step: 2,
    },
    {
      id: "Q4: Regarding the problem/pain your prospects face, how does it make them feel BEFORE and AFTER becoming a customer?",
      label:
        "4. Regarding the problem/pain your prospects face, how does it make them feel BEFORE and AFTER becoming a customer?",
      step: 2,
    },
    {
      id: "Q5: What is fundamentally unfair or 'just plain wrong' about your prospects' life currently?",
      label:
        "5. What is fundamentally unfair or 'just plain wrong' about your prospects' life currently?",
      step: 2,
    },
    {
      id: "Q6: If a prospect chooses not to adopt your product/service, what is the greatest risk or pain they may/will encounter?",
      label:
        "6. If a prospect chooses not to adopt your product/service, what is the greatest risk or pain they may/will encounter?",
      step: 2,
    },
    {
      id: "Q7: What is the single most common mistake you've seen people make when trying to solve the problem themselves? And, how does it usually end up costing them?",
      label:
        "7. What is the single most common mistake you've seen people make when trying to solve the problem themselves? And, how does it usually end up costing them?",
      step: 2,
    },
    {
      id: 'Q8: What key insight or statistic do most of your prospects not realize that they need to "wake up" to?',
      label:
        '8. What key insight or statistic do most of your prospects not realize that they need to "wake up" to?',
      step: 3,
    },
    {
      id: "Q9: What ought to make your prospects' decision to act now a 'no-brainer'?",
      label:
        "9. What ought to make your prospects' decision to act now a 'no-brainer'?",
      step: 3,
    },
    {
      id: "Q10: What is the single greatest benefit a customer will get from your product or service?",
      label:
        "10. What is the single greatest benefit a customer will get from your product or service?",
      step: 3,
    },
    {
      id: "Q11: Name a second benefit a customer will get from your product or service?",
      label:
        "11. Name a second benefit a customer will get from your product or service.",
      step: 3,
    },
    {
      id: "Q12: Name a third benefit a customer will get from your product or service",
      label:
        "12. Name a third benefit a customer will get from your product or service.",
      step: 3,
    },
    {
      id: 'Q13: Describe the "transformed identity" which customers enjoy after experiencing your product? And how will they feel?',
      label:
        '13. Describe the "transformed identity" which customers enjoy after experiencing your product? And how will they feel?',
      step: 3,
    },
    {
      id: "Q14: Regarding the problem you solve, what did your founder(s) learn or experience which gave them passion to help others solve this?",
      label:
        "14. Regarding the problem you solve, what did your founder(s) learn or experience which gave them passion to help others solve this?",
      step: 4,
    },
    {
      id: "Q15: When compared to your competitors, what's the main reason prospects should choose your brand?",
      label:
        "15. When compared to your competitors, what's the main reason prospects should choose your brand?",
      step: 4,
    },
    {
      id: "Q16: Give us an example of how many other customers you've helped and/or the average results you've helped them achieve.",
      label:
        "16. Give us an example of how many other customers you've helped and/or the average results you've helped them achieve.",
      step: 4,
    },
    {
      id: `Q17: Regarding this problem, what's the "value assurance" your brand offers?`,
      label:
        '17. Regarding this problem, what\'s the "value assurance" your brand offers?',
      step: 4,
    },
    {
      id: 'Q18: Provide us with an "ideal" sample testimonial which demonstrates the transformational power of your product/service. Make sure it includes how they felt before, AND how they feel now.',
      label:
        '18. Provide us with an "ideal" sample testimonial which demonstrates the transformational power of your product/service. Make sure it includes how they felt before, AND how they feel now.',
      step: 5,
    },
    {
      id: "Q19: What's your teams' combined experience, certification, or credentials that demonstrates your competency?",
      label:
        "19. What's your team's combined experience, certification, or credentials that demonstrates your competency?",
      step: 5,
    },
    {
      id: "Q20: What guarantee can you offer that will put prospects at ease?",
      label:
        "20. What guarantee can you offer that will put prospects at ease?",
      step: 6,
    },
    {
      id: "Q21: What can you give away as a FREE tool, guide, or email course to incentivize prospects to give you their email even if they aren't planning on becoming a customer right away? Write a title for this offering",
      label:
        "21. What can you give away as a FREE tool, guide, or email course to incentivize prospects to give you their email even if they aren't planning on becoming a customer right away? Write a title for this offering.",
      step: 6,
    },
    {
      id: "Q22: What are 3 easy steps prospects should take to achieve their desired outcome with your product/service?",
      label:
        "22. What are 3 easy steps prospects should take to achieve their desired outcome with your product/service?",
      step: 6,
    },
    {
      id: "Q23: What's the first easy step you'd like prospects to take in order to get started?",
      label:
        "23. What's the first easy step you'd like prospects to take in order to get started?",
      step: 6,
    },
  ];

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const validateForm = () => {
    const newErrors = {};
    questions.forEach((question) => {
      if (!formData[question.id]) {
        newErrors[question.id] = "This field is required";
      }
    });
    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleNext = () => {
    setStep(step + 1);
  };

  const handlePrevious = () => {
    setStep(step - 1);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setSubmitted(true);
    if (validateForm()) {
      try {
        await base("Marketing Script").create([
          {
            fields: formData,
          },
        ]);

        alert("Form submitted successfully!");
      } catch (error) {
        alert("Uh Oh! Form submission error");
      }
    }
  };

  const renderQuestion = (question) => (
    <div key={question.id} style={styles.questionContainer}>
      <label htmlFor={question.id} style={styles.label}>
        {question.label}
      </label>
      <p style={styles.placeholder}>
        Lorem ipsum dolor sit amet, consectetur adipiscing elit.
      </p>
      {question.type === "radio" ? (
        <div style={styles.radioGroup}>
          <label style={styles.radioLabel}>
            <input
              type="radio"
              name={question.id}
              value="yes"
              checked={formData[question.id] === "yes"}
              onChange={handleInputChange}
              style={styles.radioInput}
            />
            Yes
          </label>
          <label style={styles.radioLabel}>
            <input
              type="radio"
              name={question.id}
              value="no"
              checked={formData[question.id] === "no"}
              onChange={handleInputChange}
              style={styles.radioInput}
            />
            No
          </label>
        </div>
      ) : (
        <input
          type="text"
          id={question.id}
          name={question.id}
          value={formData[question.id] || ""}
          onChange={handleInputChange}
          style={{
            ...styles.input,
            ...(submitted && errors[question.id] ? styles.inputError : {}),
          }}
        />
      )}
      {submitted && errors[question.id] && (
        <p style={styles.errorText}>{errors[question.id]}</p>
      )}
    </div>
  );

  const hasErrors = (stepNumber) => {
    return questions
      .filter((q) => q.step === stepNumber)
      .some((q) => !formData[q.id]);
  };

  return (
    <div style={styles.container}>
      <div style={styles.breadcrumbs}>
        {steps.map((s) => (
          <button
            key={s.number}
            onClick={() => setStep(s.number)}
            style={{
              ...styles.breadcrumbButton,
              ...(s.number === step ? styles.activeBreadcrumb : {}),
              ...(submitted && hasErrors(s.number)
                ? styles.errorBreadcrumb
                : {}),
            }}
          >
            {`Step ${s.number}`}
          </button>
        ))}
      </div>
      <h1 style={styles.header}>{steps.find((s) => s.number === step).name}</h1>
      <form onSubmit={handleSubmit} style={styles.form}>
        {questions.filter((q) => q.step === step).map(renderQuestion)}
        <div style={styles.buttonContainer}>
          {step > 1 && (
            <button
              type="button"
              onClick={handlePrevious}
              style={styles.secondaryButton}
            >
              Back
            </button>
          )}
          {step < 6 ? (
            <button
              type="button"
              onClick={handleNext}
              style={styles.primaryButton}
            >
              Next
            </button>
          ) : (
            <button type="submit" style={styles.primaryButton}>
              Submit
            </button>
          )}
        </div>
      </form>
    </div>
  );
};

const styles = {
  container: {
    maxWidth: "1000px",
    width: "80vw",
    margin: "0 auto",
    padding: "40px",
    fontFamily: "'Inter', Arial, sans-serif",
    fontSize: "16px",
    color: "#202124",
    backgroundColor: "#f9fafb",
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  },
  breadcrumbs: {
    display: "flex",
    justifyContent: "space-between",
    marginBottom: "40px",
    width: "100%",
    flexWrap: "nowrap",
  },
  breadcrumbButton: {
    padding: "7.5px",
    border: "none",
    background: "none",
    cursor: "pointer",
    fontSize: "10px",
    color: "#475467",
    fontFamily: "'Spartan', Arial, sans-serif",
    transition: "color 0.3s ease",
    whiteSpace: "nowrap",
    overflow: "hidden",
    textOverflow: "ellipsis",
    flex: "1 1 auto",
    "&:hover": {
      filter: "brightness(0.9)",
    },
  },
  activeBreadcrumb: {
    fontWeight: "bold",
    color: "#0e9384",
    borderBottom: "3px solid #0e9384",
  },
  errorBreadcrumb: {
    color: "#d93025",
    borderBottom: "3px solid #d93025",
  },
  header: {
    fontSize: "18px",
    marginBottom: "20px",
    fontWeight: "bold",
    fontFamily: "'Spartan', Arial, sans-serif",
    alignSelf: "flex-start",
  },
  form: {
    display: "flex",
    flexDirection: "column",
    width: "100%",
  },
  questionContainer: {
    marginBottom: "12px",
    textAlign: "left",
    width: "100%",
  },
  label: {
    display: "block",
    marginBottom: "6px",
    fontSize: "16px",
    fontWeight: "500",
    fontFamily: "'Inter', Arial, sans-serif",
  },
  placeholder: {
    fontSize: "14px",
    color: "#5f6368",
    marginBottom: "9px",
    fontFamily: "'Inter', Arial, sans-serif",
  },
  input: {
    width: "100%",
    padding: "15px",
    fontSize: "16px",
    border: "1px solid #d0d5dd",
    borderRadius: "4px",
    fontFamily: "'Inter', Arial, sans-serif",
  },
  inputError: {
    borderColor: "#d93025",
  },
  radioGroup: {
    display: "flex",
    gap: "30px",
  },
  radioLabel: {
    display: "flex",
    alignItems: "center",
    fontSize: "16px",
    fontFamily: "'Inter', Arial, sans-serif",
  },
  radioInput: {
    marginRight: "10px",
    width: "20px",
    height: "20px",
  },
  errorText: {
    color: "#d93025",
    fontSize: "14px",
    marginTop: "5px",
    fontFamily: "'Inter', Arial, sans-serif",
  },
  buttonContainer: {
    display: "flex",
    justifyContent: "flex-end",
    marginTop: "40px",
    width: "100%",
  },
  primaryButton: {
    padding: "15px 30px",
    backgroundColor: "#ccfcf0",
    color: "#0e9384",
    border: "1px solid #0e9384",
    borderRadius: "4px",
    cursor: "pointer",
    fontSize: "16px",
    fontWeight: "500",
    fontFamily: "'Spartan', Arial, sans-serif",
    transition: "background-color 0.3s ease, color 0.3s ease",
  },
  secondaryButton: {
    padding: "15px 30px",
    backgroundColor: "#ccfcf0",
    color: "#0e9384",
    border: "1px solid #0e9384",
    borderRadius: "4px",
    cursor: "pointer",
    fontSize: "16px",
    fontWeight: "500",
    fontFamily: "'Spartan', Arial, sans-serif",
    transition: "background-color 0.3s ease, color 0.3s ease",
    marginRight: "10px",
  },
};

export default MultiStepForm;
